<template>
  <div id="map"></div>
</template>

<script>
// import { getMap } from "@/api/https";
import AMapLoader from "@amap/amap-jsapi-loader";
export default {
  data() {
    return {
      key: "fd6697ba33023f67e965a65f8abd69a3",
    };
  },
  async mounted() {
    this.initMap();
  },
  methods: {
    //
    // 
    initMap() {
      AMapLoader.load({
        key: this.key, // WebKey load 
        version: "2.0", //  JSAPI  1.4.15
        plugins: [""], // 'AMap.Scale'
      })
        .then((AMap) => {
			// console.log(AMap);
          this.map = new AMap.Map("map", {
            pitch:65, //083
            viewMode: "3D", //viewMode3D
            zoom: 17, //
            expandZoomRange: true, // expandZoomRange  true  zoomsPC20
            zooms: [3, 18], //PC[3,18],[3-18];,[3,19],[3-19],19/20
            center: [115.15,36.28], //
			icon: require("@/assets/icon.png"),
			
		});
			var marker = new AMap.Marker({
				position: [115.15, 36.28], // 
				title:'',
				map: this.map, // 	
			})
		
			marker.on('click', function (p) {
				// console.log(p,123456789);
				// 
				var infoWindow = new AMap.InfoWindow({
					content: '',
					offset: new AMap.Pixel(0, -30),
				});
				infoWindow.open(this.map, marker.getPosition());
			});

		marker.setMap(this.map)
        })
        .catch((e) => {
        //   console.log(e);
        });
		
    },
  },
};
</script>

<style lang="scss" scoped>
#map {
  width: 80%;
  height: 100%;
}
</style>