<template>
	<div class="gantt">
		<div class="title" @click="open">Click Gantt</div>
		<div class="" @click="close"></div>
	  <div id="gstc">
		<Gantt/>
	</div>
  </div>
</template>

<script>
import Gantt from '@/components/gantt'
export default {
	components:{
		Gantt
	},
  data() {
    return {
		flag:false,
		
	};
  },
  mounted() {

  },
  methods:{
	close(){
		this.flag=false
		// console.log('');
		window.clearInterval(this.Interval)
	},
	open(){
		// console.log('');
		this.flag=true
		this.Interval=window.setInterval(()=>{
			console.log('');
		},10000)
		setTimeout(()=>{
			this.Interval=window.setInterval(()=>{
				console.log('');
			},10000)
		},5000)
	}
  }
};
</script>

